import React from 'react'

const data = {
    "https://github.com/tanateros": "Github",
    "https://github.com/tanateros/way-search": "All Different Directions",
    "https://github.com/tanateros/easy-rest-api": "easy-rest-api",
    "https://github.com/tanateros/zf2-doctrine2": "ZF2 with Doctrine2 for RESTfull API skeleton",
    "https://github.com/tanateros/short-link-creator": "short-link-creator",
    "https://github.com/tanateros/symfony-2.8-request-test": "My old test task",
};

export default () => (
    <div className='row'>
        <div className='col-md-12'>
            <table className="table table-bordered table-responsive">
                <thead>
                <tr>
                </tr>
                </thead>
                <tbody>
                {Object.keys(data).map(item => (
                    <tr>
                        <th scope="row">{data[item]}</th>
                        <td><a href={item}>{item}</a></td>
                    </tr>
                ))}
                </tbody>
            </table>
        </div>
    </div>
)



