import React from 'react'

const data = {
    "Education": "20062011  Kiev State Maritime Academy, Engineer Developer programmer",
    "Experience in IT": "12 years",
    "Minimal salary for month": "$3000",
    "Age": "29",
};

export default () => (
    <div className='row'>
        <div className='col-md-12'>
            <table className="table table-bordered table-responsive">
                <thead>
                <tr>
                </tr>
                </thead>
                <tbody>
                {Object.keys(data).map(item => (
                    <tr>
                        <th scope="row">{item}</th>
                        <td>{data[item]}</td>
                    </tr>
                ))}
                <tr>
                    <th scope="row">Languages</th>
                    <td>
                        <td>
                            <th scope="row">Russian, Ukrainian</th>
                            <td>native</td>
                        </td>
                        <td>
                            <th scope="row">English</th>
                            <td>read-write: intermediate; speak: beginner</td>
                        </td>
                    </td>
                </tr>
                </tbody>
            </table>
            <div className="alert alert-danger" role="alert">
                Work type: only remote
            </div>
        </div>
    </div>
)



