import React from 'react'
import Octicon, {DeviceMobile, DeviceDesktop, Megaphone, Mail} from '@githubprimer/octicons-react'

export default () => (
    <div className='col-md-12'>
        <p>
            <a className="btn btn-primary" data-toggle="collapse" href="#contacts" role="button" aria-expanded="false" aria-controls="contacts">Contacts</a>
        </p>
        <div className="row">
            <div className="col">
                <div className="collapse multi-collapse" id="contacts">
                    <div className="card card-body">
                        <div className="row">
                            <div className="col-4">
                                <div className="list-group" id="list-tab" role="tablist">
                                    <a className="list-group-item list-group-item-action active" id="list-home-list" data-toggle="list" href="#list-phones" role="tab" aria-controls="home">
                                        <Octicon icon={DeviceMobile}/>
                                        <span className="d-none d-lg-block d-print-block">Phones</span>
                                    </a>
                                    <a className="list-group-item list-group-item-action" id="list-profile-list" data-toggle="list" href="#list-profile" role="tab" aria-controls="profile">
                                        <Octicon icon={DeviceDesktop}/>&nbsp;
                                        <span className="d-none d-lg-block d-print-block">Skype</span>
                                    </a>
                                    <a className="list-group-item list-group-item-action" id="list-messages-list" data-toggle="list" href="#list-messages" role="tab" aria-controls="messages">
                                        <Octicon icon={Megaphone}/>&nbsp;
                                        <span className="d-none d-lg-block d-print-block">Telegram</span>
                                    </a>
                                    <a className="list-group-item list-group-item-action" id="list-settings-list" data-toggle="list" href="#list-settings" role="tab" aria-controls="settings">
                                        <Octicon icon={Mail}/>&nbsp;
                                        <span className="d-none d-lg-block d-print-block">E-mail</span>
                                    </a>
                                </div>
                            </div>
                            <div className="col-8">
                                <div className="tab-content" id="nav-tabContent">
                                    <div className="tab-pane fade show active" id="list-phones" role="tabpanel" aria-labelledby="list-home-list">
                                        (+38) 099-622-2920<br/>(+38) 063-954-6663
                                    </div>
                                    <div className="tab-pane fade" id="list-profile" role="tabpanel" aria-labelledby="list-profile-list">
                                        tanateros89
                                    </div>
                                    <div className="tab-pane fade" id="list-messages" role="tabpanel" aria-labelledby="list-messages-list">
                                        tgmyname
                                    </div>
                                    <div className="tab-pane fade" id="list-settings" role="tabpanel" aria-labelledby="list-settings-list">
                                        tanaterosy@gmail.com
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
)



