import React from 'react';
import {Link} from 'react-router-dom'

import logo from '../../ava.jpeg';

const menuItems = {
    '/': "Yuriy Malinovskiy",
    '/experience': "WORK EXPERIENCE",
    '/code-examples': "CODE EXAMPLES",
    '/contact': "CONNECT WITH ME",
};

export default class App extends React.Component {
    constructor() {
        super();
        this.state = {
            active: 'none'
        };
        this._handleClick = this._handleClick.bind(this);
    }

    _handleClick(menuItem) {
        this.setState({ active: menuItem });
    }

    render() {
        return (
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <a className="navbar-brand" href="#">
                    <img src={logo} className="App-logo" alt="logo" width="30" height="30" />
                </a>
                <Link to="/" className="navbar-brand">Developer</Link>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"> </span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">
                        {Object.keys(menuItems).map(item => (
                            <li
                                key={item}
                                className={this.state.active === item ? "nav-item active" : "nav-item"}
                            >
                                <Link
                                    className="nav-link"
                                    onClick={this._handleClick.bind(this, item)}
                                    to={item}
                                >
                                    {menuItems[item]}
                                </Link>
                            </li>
                        ))}
                        <li className="nav-item dropdown">
                            <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                SKILLS
                            </a>
                            <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                                <a className="dropdown-item" href="#">PROFESSIONAL SKILLS</a>
                                <a className="dropdown-item" href="#">ADDITIONAL SKILLS</a>
                                <div className="dropdown-divider"> </div>
                                <a className="dropdown-item" href="#">PERSONAL SKILLS</a>
                            </div>
                        </li>
                    </ul>
                    {/*<form className="form-inline my-2 my-lg-0">*/}
                    {/*<input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search" />*/}
                    {/*<button className="btn btn-outline-success my-2 my-sm-0" type="submit">Search allow technology</button>*/}
                    {/*</form>*/}
                </div>
            </nav>
        )
    }
}



